// Internal Import
import * as React from "react";

// CSS Import
import '../styles/global.css';

// SVG Import
import IconStar from "../../static/assets/StarLine.svg";

const Collapse = ({  title = "template", children = "Lorem Ipsum", index, type, className, btnClassName, contentClassName, state = false }) => {
    const [radio, setRadio] = React.useState(state);

    function CheckState() {
        if(radio === true){
            return("true");
        }else{
            return("false");
        }
    }

    switch (type) {
        case "normal":
            return (
                <label className={`${className}`}>
                    <button type="button" className={`${btnClassName}`} onClick={() => {setRadio(radio === false ? true : false)}}>
                        {title}
                    </button>
                    <p className={`${radio === true ? "translate-y-0 opacity-100" : "absolute w-0 h-0 translate-y-6 overflow-hidden opacity-0"} ${contentClassName} transition duration-300 ease-in-out`}>
                        {children}
                    </p>
                </label>
            );
        default:
            return (
                <label className={`${radio === true ? `${className}` : "grid translate-x-0"} group hover:bg-tertiary-400/20 cursor-pointer transition-all duration-700 ease-in-out`}>
                    <button type="button" className={`${btnClassName}`} onClick={() => {setRadio(radio === false ? true : false)}}>
                        <div className={`${radio === true ? "text-tertiary-400 group-hover:text-primary" : "text-tertiary-400 translate-x-[20%] w-full opacity-0 absolute"} flex content-start gap-x-4 text-3xl font-medium font-plusJakartaSans transition duration-700 ease-in-out`}>
                            <span className={`text-base font-playfairDisplay italic`}>{index}</span> {title}
                            <p>Test {CheckState()}</p>
                        </div>
                        <div className={`${radio === true ? "text-transparent bg-transparent w-0 h-0 overflow-hidden opacity-0 absolute -translate-x-12" : "text-tertiary-400 group-hover:text-primary/50 w-full justify-center"} flex content-start gap-x-4 text-3xl font-medium font-plusJakartaSans transition duration-700 ease-in-out`}>
                            <span className={`text-base font-playfairDisplay italic`}>{index}</span> {title}
                            <p>Test {CheckState()}</p>
                        </div>
                    </button>
                    <p className={`${radio === true ? `${contentClassName}` : "absolute w-0 h-0 translate-y-6 text-transparent bg-transparent overflow-hidden opacity-0 -z-20"} text-tertiary-300 group-hover:text-tertiary-100 transition duration-700 ease-in-out`}>
                        <IconStar className={`${radio === true ? "text-4xl w-8 h-8 text-primary" : "hidden"}`} />
                        {children}
                        <p>Test {CheckState()}</p>
                    </p>
                </label>
            );
    }
};
 
export default Collapse;