// Internal Import
import * as React from "react";
import Header from "./Header";

// CSS Import
import '../styles/global.css';

const Layout = ({ children, className }) => {
    return (
        <div className={`${className} flex flex-col min-h-screen overflow-x-hidden bg-tertiary-500`}>
            <Header />
            { children }
        </div>
    );
}
 
export default Layout;