// Internal Import
import * as React from "react";

// CSS Import
import '../styles/global.css';

// SVG Import
import IconArrowTR from "../../static/assets/TopRightArrow.svg";
import IconArrowB from "../../static/assets/BottomArrow.svg";

const PrimaryBtn = ({ children, className, type, custom = false, color = "primary", onClick = null, href, tabIndex, disabled = false }) => {
    // Set Btn Variable
    const [btnColor, setBtnColor] = React.useState(color);
    const [btnType, setBtnType] = React.useState(type);

    // Set Function
    function setColor (color) {
        switch (color) {
            case "primary":
                return(
                    "text-tertiary-500 bg-primary border-primary hover:text-white hover:bg-transparent"
                )
            case "reverse-primary":
                return(
                    "text-tertiary-500 bg-transparent border-white text-white hover:bg-transparent hover:border-primary"
                )
            default:
                return(
                    "text-tertiary-500 bg-primary border-primary hover:text-white hover:bg-transparent"
                )
        }
    };

    function setCustom (custom) {
        if(custom === false){
            return (
                `flex items-center p-2 px-8 ${className} font-plusJakartaSans font-medium border rounded-full duration-500 ease-in-out`
            )
        }else{
            return (
                `${className} font-plusJakartaSans font-medium border transition duration-500 ease-in-out`
            )
        }
    };

    // Set Use Effect
    React.useEffect(() => {
        setBtnColor(color);
        setBtnType(type);
    },[ color, type ])

    switch (btnType) {
        case "link":
            return (
                <button className={`group relative ${setCustom(custom)} ${setColor(btnColor)}`} type="button" onClick={onClick =! null ? onClick : ""} tabIndex={tabIndex} disabled={disabled}>
                    <a href={href}>
                        {children}
                    </a>
                </button>
            );
        case "button":
            return (
                <button className={`group relative ${setCustom(custom)} ${setColor(btnColor)}`} type="button" onClick={onClick =! null ? onClick : ""} tabIndex={tabIndex} disabled={disabled}>
                    <p className="translate-x-3 group-hover:-translate-x-4 transition-all duration-500 ease-in-out">{children}</p>
                    <div className="absolute grid place-content-center w-8 h-8 bg-tertiary-500 text-primary group-hover:bg-white group-hover:text-tertiary-500 rounded-full transition-all duration-500 ease-in-out animateSlideRight">
                        <IconArrowTR />
                    </div>
                </button>
            );
        case "small-button":
            return (
                <button className={`group relative ${setCustom(true)} grid place-content-center w-8 h-8 bg-tertiary-500 text-primary hover:bg-primary hover:text-tertiary-500 border-[linear-gradient(135deg,#FFFFFF_2.88%,rgba(255,255,255,0)_102.1%)] hover:border-primary rounded-full transition-all duration-500 ease-in-out`} type="button" onClick={onClick =! null ? onClick : ""} tabIndex={tabIndex} disabled={disabled}>
                    <IconArrowB className="text-2xl w-4 h-4" />
                </button>
            );
        case "scroll-button":
            return (
                <button className={`group relative py-[26px] px-14 ${setCustom(false)} ${setColor(btnColor)}`} type="button" onClick={onClick =! null ? onClick : ""} tabIndex={tabIndex} disabled={disabled}>
                    <div className="flex flex-nowrap whitespace-nowrap gap-x-24 relative translate-x-[50px] group-hover:-translate-x-14 overflow-hidden transition-all duration-500 ease-in-out">
                        <p className="RightToLeft flex flex-nowrap whitespace-nowrap text-white">{children}</p>
                    </div>
                    <div className="absolute grid place-content-center w-20 h-20 bg-white text-tertiary-500 group-hover:bg-primary group-hover:text-tertiary-500 rounded-full transition-all duration-500 ease-in-out animateDashRight">
                        <IconArrowTR className="text-3xl w-8 h-8" />
                    </div>
                </button>
            );
        default:
            return (
                <button className={`group relative ${setCustom(custom)} ${setColor(btnColor)}`} type="button" onClick={onClick =! null ? onClick : ""} tabIndex={tabIndex} disabled={disabled}>
                    <p className="-translate-x-4 group-hover:translate-x-4 transition-all duration-500 ease-in-out">{children}</p>
                    <div className="absolute grid place-content-center w-8 h-8 bg-tertiary-500 text-primary group-hover:bg-white group-hover:text-tertiary-500 rounded-full transition-all duration-500 ease-in-out animateSlideRight">
                        <IconArrowTR />
                    </div>
                </button>
            );
    }
};
export default PrimaryBtn;