// Internal Import
import * as React from "react";

// CSS Import
import '../styles/global.css';

// SVG Import
import IconStarImg from "../../static/assets/StarIcon.svg";
import IconStar from "../../static/assets/StarLine.svg";

// Import Component
import Layout from "../components/Layout";
import PrimaryBtn from "../components/PrimaryBtn";
import Collapse from "../components/Collapse";

export default function Home() {
  return (
    <Layout className="relative pb-16 text-white font-plusJakartaSans">
      <section className="container grid mt-52">
        <div className="flex items-center justify-center 3xl:justify-start gap-x-6">
          <h2 className="text-[5rem] 3xl:text-[6.25rem] uppercase">Bring your brilliant</h2>
          <ul className="grid content-center gap-y-3 h-full text-tertiary-200">
            <li className="flex items-center gap-x-1 translate-x-9">I made something <span className="font-extrabold italic">COOL</span></li>
            <li className="flex items-center gap-x-1 translate-x-4">I made something <span className="font-extrabold italic">CRAZY</span></li>
            <li className="flex items-center gap-x-1 translate-x-0">I made something <span className="font-extrabold italic">NEW</span></li>
          </ul>
        </div>
        <div className="flex items-center justify-center 3xl:justify-start gap-x-3">
          <IconStarImg className="w-[6.25rem] h-[6.25rem]" />
          <h3 className="text-[5rem] 3xl:text-[6.25rem] uppercase">Ideas into</h3>
          <div className="grid content-center">
            <PrimaryBtn type="scroll-button" color="reverse-primary" className="flex w-[198px] overflow-hidden"><IconStar className="text-primary pr-2" /> Availabe for work</PrimaryBtn>
          </div>
          <h3 className="text-[5rem] 3xl:text-[6.25rem] uppercase">Reality</h3>
        </div>
        <div className="grid w-full h-1 mt-20 mb-4 border-b border-b-tertiary-300"></div>
        <div className="flex justify-between text-tertiary-300">
          <p>Product Designer // From Indonesia.</p>
          <p>2023</p>
        </div>
      </section>
      
      <section className="container grid mt-52">
        <div className="grid place-content-center">
          <p className="text-center text-3xl font-plusJakartaSans max-w-[915px]">
            <span className="font-plusJakartaSans">Ahmad Dimas is a skilled</span>
            <span className="font-playfairDisplay font-medium italic"> UI/UX Designer </span> 
            <span className="font-plusJakartaSans">and 3D Artist with nearly two years of experience. Ahmad's ability to  </span>
            <span className="font-playfairDisplay font-medium italic"> Bring ideas to life </span>
            <span className="font-plusJakartaSans">make him a </span>
            <span className="font-playfairDisplay font-medium italic"> Valuable asset </span>
            <span className="font-plusJakartaSans">to any project, including web, mobile app, ASO, and more.</span>
          </p>
        </div>
        <div className="flex justify-center mt-14">
          <PrimaryBtn>Say hello</PrimaryBtn>
        </div>
        <div className="flex justify-center mt-40">
          <PrimaryBtn type="small-button"></PrimaryBtn>
        </div>
      </section>

      <section className="container grid mt-52">
        <h2 className="text-center font-medium text-5xl mb-16">
          <span className="font-plusJakartaSans">My </span><span className="font-playfairDisplay italic">Expertise</span>
        </h2>
        <div className="grid">
          <Collapse index="01" title="Website Design" state={true}
            className="grid grid-cols-2 min-h-[140px] transition-all duration-300 ease-in-out"
            btnClassName="flex content-start justify-start min-h-[140px] gap-x-4 px-8 py-12"
            contentClassName="flex content-start justify-end min-h-[140px] gap-x-6 p-8 font-plusJakartaSans"
          >
            <p className="max-w-[420px] text-base tracking-tight">
              <span>Starting by learn and research based on client brief and resource to.</span>
              <span className="font-bold italic text-tertiary-100"> gain about the needs, goals, product </span>
              <span>and design requirements</span>
            </p>
          </Collapse>
          <div className="grid w-full h-0 my-2 border-b border-b-tertiary-300"></div>
          <Collapse index="02" title="App Store Optimization" state={true}
            className="grid grid-cols-2 min-h-[140px] transition-all duration-300 ease-in-out"
            btnClassName="flex content-start justify-start min-h-[140px] gap-x-4 px-8 py-12"
            contentClassName="flex content-start justify-end min-h-[140px] gap-x-6 p-8 font-plusJakartaSans"
          >
            <p className="max-w-[420px] text-base tracking-tight">
              <span>With my expertise in app store optimization design, I can significantly </span>
              <span className="font-bold italic text-tertiary-100">enhance the visibility of your app,</span>
              <span> boosting its downloads and revenue.</span>
            </p>
          </Collapse>
          <div className="grid w-full h-0 my-2 border-b border-b-tertiary-300"></div>
          <Collapse index="03" title="Web Development" 
            className="grid grid-cols-2 min-h-[140px] transition-all duration-300 ease-in-out"
            btnClassName="flex content-start justify-start min-h-[140px] gap-x-4 px-8 py-12"
            contentClassName="flex content-start justify-end min-h-[140px] gap-x-6 p-8 font-plusJakartaSans"
          >
            <p className="max-w-[420px] text-base tracking-tight">
              <span>With my in-depth knowledge and extensive experience in web development, I have the ability to transform your </span>
              <span className="font-bold italic text-tertiary-100">design vision into a stunning reality.</span>
            </p>
          </Collapse>
          <div className="grid w-full h-0 my-2 border-b border-b-tertiary-300"></div>
          <Collapse index="04" title="Mobile App" 
            className="grid grid-cols-2 min-h-[140px] transition-all duration-300 ease-in-out"
            btnClassName="flex content-start justify-start min-h-[140px] gap-x-4 px-8 py-12"
            contentClassName="flex content-start justify-end min-h-[140px] gap-x-6 p-8 font-plusJakartaSans"
          >
            <p className="max-w-[420px] text-base tracking-tight">
              <span>With my expertise in user experience and user interface design, I can take your </span>
              <span className="font-bold italic text-tertiary-100">custom design app </span>
              <span>to the next level of perfection.</span>
            </p>
          </Collapse>
          <div className="grid w-full h-0 my-2 border-b border-b-tertiary-300"></div>
          <Collapse index="05" title="Pitch deck" 
            className="grid grid-cols-2 min-h-[140px] transition-all duration-300 ease-in-out"
            btnClassName="flex content-start justify-start min-h-[140px] gap-x-4 px-8 py-12"
            contentClassName="flex content-start justify-end min-h-[140px] gap-x-6 p-8 font-plusJakartaSans"
          >
            <p className="max-w-[420px] text-base tracking-tight">
              <span>With my expertise in pitch deck design, I can help you create a compelling and </span>
              <span className="font-bold italic text-tertiary-100">visually stunning presentation </span>
              <span>that will leave a lasting impression on your audience.</span>
            </p>
          </Collapse>
          <div className="grid w-full h-0 my-2 border-b border-b-tertiary-300"></div>
          <Collapse index="06" title="3D Design" 
            className="grid grid-cols-2 min-h-[140px] transition-all duration-300 ease-in-out"
            btnClassName="flex content-start justify-start min-h-[140px] gap-x-4 px-8 py-12"
            contentClassName="flex content-start justify-end min-h-[140px] gap-x-6 p-8 font-plusJakartaSans"
          >
            <p className="max-w-[420px] text-base tracking-tight">
              <span>By utilizing the latest tools and techniques in 3D modeling , texturing, and rendering, I can create </span>
              <span className="font-bold italic text-tertiary-100">stunning and lifelike visualizations </span>
              <span>that bring your ideas to life.</span>
            </p>
          </Collapse>
        </div>
      </section>

      <section className="container grid mt-52">
        <h2 className="grid justify-center gap-y-2 text-center font-medium font-plusJakartaSans text-5xl uppercase bg-purple-500">
          <span>Design is not just what it</span>
          <span>Looks like and feels like.</span>
          <span>Design is how it Works.</span>
        </h2>
      </section>
    </Layout>
  )
}
